import React, { Component } from "react";

export class Section2 extends Component {
  render() {
    return (
      <div className="section section2">
        <div className="section-title section-title-side">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">01</h6>
          </div>
          <h5>What is DevOps as a service?</h5>
          <p>Our team takes care onrunning everything,so yourdeveloper teamcan focus purely on development.</p>
        </div>
        <div className="graph-image"></div>
        <div className="list">
          <div className="list-item">
            <div className="list-item-logo list-item-logo1"></div>
            <div className="list-item-text">
              <h6>Users</h6>
              <p>Easy and flexible management of user's rights and access.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo2"></div>
            <div className="list-item-text">
              <h6>CI/CD</h6>
              <p>All components smoothly integrates into CI/CD process.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo3"></div>
            <div className="list-item-text">
              <h6>Environments</h6>
              <p>
                Code can be written on the local programmer's device, then transferred and merged with the main code. You can use many different environments for your individual processes (Test, Dev,
                Build, Prod, ...).
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo4"></div>
            <div className="list-item-text">
              <h6>Integrations</h6>
              <p>You can integrate your projects with all the most popular communication, correspondence, task management programs (Jira, Slack, MS Teams, Asana ...)</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Section2;
