import React, { Component } from "react";

export class Section3 extends Component {
  render() {
    return (
      <div className="section section3">
        <div className="section-title section-title-side">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">02</h6>
          </div>
          <h5>How it works?</h5>
        </div>
        <div className="list">
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Green-blue deployment out of the box.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Separate environments for testing, staging, demo, you name it!</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Application updates deployed with zero downtime.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Full see-through observability of performance via graphs and logs.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Trackable change management system.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Ability to quickly roll-backto any configuration, iteration or application version at any time.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Instant access to professional DevOps engineers with their expertise.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>You get 24/7/365, best in industry support.</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Section3;
