import React, { Component } from "react";

export class Section7 extends Component {
  render() {
    return (
      <div className="section section7">
        <div className="section-title section-title-side">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">06</h6>
          </div>
          <h5>Try demo</h5>
          <p>Register here and you will get a demo account</p>
        </div>
        <div className="email-container">
          <input type="email" name="" id="" placeholder="Your email address" className="input" />
          <div className="btn btn-round">Get demo account</div>
        </div>
      </div>
    );
  }
}

export default Section7;
