import React, { Component } from "react";
import $ from "jquery";

export class Section1 extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.scrollToSection = this.scrollToSection.bind(this);
  }

  scrollToSection(element) {
    const projectsOffset = $("." + element).offset().top - 90;
    $("html,body").animate({ scrollTop: projectsOffset }, "slow");
  }

  render() {
    return (
      <div className="section section1">
        <div className="video-container"></div>
        <div className="text-container">
          <h1>DevOps as a service</h1>
          <p>Best-in-class DevOps practices, skills, tools and infrastructure for system containerization</p>

          <div className="btn btn-learn-more btn-round" onClick={() => this.scrollToSection("section2")}>
            Learn more
          </div>
        </div>
      </div>
    );
  }
}

export default Section1;
