import React, { Component } from "react";

export class Section6 extends Component {
  render() {
    return (
      <div className="section section6">
        <div className="section-title section-title-center">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">05</h6>
            <div className="line line2"></div>
          </div>
          <h5>Onboarding</h5>
        </div>
        <div className="list">
          <div className="list-item">
            <div className="list-item-logo list-item-logo1">1</div>
            <div className="list-item-text">
              <h6>Infrastructure analysis</h6>
              <p>
                We will analyse your current infrastructure, challenges you face and overall systems specifications to get full understanding of what is the best approach for migration processes to
                DevOps for you specifically.
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo2">2</div>
            <div className="list-item-text">
              <h6>Consultation on migration</h6>
              <p>
                We will provide consultation on migration from your legacy or 3d party infrastructure to DevOps-ready infrastructure managed by us. You may also elect to use your current
                infrastructure.
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo3">3</div>
            <div className="list-item-text">
              <h6>Migration plan defined</h6>
              <p>Together we will define migration plan to give you a clear view and understanding what and when will be done and how this will impact your operations.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo4">4</div>
            <div className="list-item-text">
              <h6>Migration</h6>
              <p>
                We will perform the upgrade of your current infrastructure and processes to DevOps level which contains a modern set of tools and processes based on best practices of CI/CD,QA, load
                balancing autoscaling, DR and backup solutions.
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo5">5</div>
            <div className="list-item-text">
              <h6>Going live</h6>
              <p>You will enjoy benefits of evolutionary leap your development process just undertook, you can now afford to focus purely on code. Congratulations!</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Section6;
